<template>
  <div class="platform-setting">
    <div class="header-sub">
      <div class="title">
        <div class="breadcrumb-item-link">平台设置</div>
        <span class="empty"></span>
      </div>
    </div>
    <div class="d-content">
      <div class="tab-flat">
        <a :class="{active:currentTab==='0'}" @click="selectTab('0')">站点配置</a>
        <a :class="{active:currentTab==='1'}" @click="selectTab('1')">邮件配置</a>
        <!--        <a :class="{active:currentTab==='2'}" @click="selectTab('2')">短信配置</a>-->
        <a :class="{active:currentTab==='3'}" @click="selectTab('3')">微信Banner设置</a>
      </div>
      <div class="tabs">
        <Tabs :value="currentTab" class="no-header tabs-inner" >
          <TabPane name="0" class="panel-table">
            <div class="tab-box">
              <div class="setting-box">
                <div class="setting-item">
                  <div class="desc">
                    <div class="name" v-show="!siteSetting.loginLogo">image</div>
                    <div class="img-display" :style="{backgroundImage:`url('${siteSetting.loginLogo}')`}" v-show="siteSetting.loginLogo"></div>
                    <SimpleImgUpload class="input-log" v-model="siteSetting.loginLogo" @on-selection-change="onDeviceModelChange" v-if="$hasAccess('platform.edit')">
                      <div class="img-add">{{$t_p('customize')}}</div>
                    </SimpleImgUpload>
                  </div>
                  <div class="more">
                    <h4>{{$t_p('login_logo')}}</h4>
                    <p>{{$t_p('config_format')}}</p>
                    <p>{{$t_p('login_big')}}</p>
                  </div>
                </div>
              </div>
              <div class="setting-box">
                <div class="setting-item">
                  <div class="desc">
                    <div class="name" v-show="!siteSetting.managerLogo">image</div>
                    <div class="img-display" :style="{backgroundImage:`url('${siteSetting.managerLogo}')`}" v-show="siteSetting.managerLogo"></div>
                    <div class="input">
                      <SimpleImgUpload class="input-log" v-model="siteSetting.managerLogo" @on-selection-change="onDeviceModelChange" v-if="$hasAccess('platform.edit')">
                        <div class="img-add">{{$t_p('customize')}}</div>
                      </SimpleImgUpload>
                    </div>
                  </div>
                  <div class="more">
                    <h4>{{$t_p('backstage_logo')}}</h4>
                    <p>{{$t_p('config_format')}}</p>
                    <p>{{$t_p('backstage_big')}}</p>
                  </div>
                </div>
              </div>
              <div class="setting-box">
                <div class="setting-item">
                  <div class="desc">
                    <div class="name" v-show="!siteSetting.browserLogo">image</div>
                    <div class="img-display" :style="{backgroundImage:`url('${siteSetting.browserLogo}')`}" v-show="siteSetting.browserLogo"></div>
                    <div class="input">
                      <SimpleImgUpload class="input-log" v-model="siteSetting.browserLogo" @on-selection-change="onDeviceModelChange" v-if="$hasAccess('platform.edit')">
                        <div class="img-add">{{$t_p('customize')}}</div>
                      </SimpleImgUpload>
                    </div>
                  </div>
                  <div class="more">
                    <h4>{{$t_p('browser_logo')}}</h4>
                    <p>{{$t_p('config_format')}}</p>
                    <p>{{$t_p('browser_big')}}</p>
                  </div>
                </div>
              </div>
            </div>
            <div class="info-line"></div>
            <div class="ops" v-if="$hasAccess('platform.edit')">
              <!--              <Button>恢复默认</Button>-->
              <Button type="primary" @click="saveLogo" >{{$t('common.message.save')}}</Button>
            </div>
          </TabPane>
          <TabPane name="1" class="panel-table">
            <Form ref="pasForm" :model="formPsw" :rules="userRules" :label-width="$flexible(10)">
              <FormItem class="setting-item" prop="sourcePasswd">
                <div class="desc">
                  <div class="name">
                    <span class="red" style="color:red;">*</span>SMTP服务器地址
                  </div>
                </div>
                <div class="input">
                  <Input
                    class="input-txt"
                    type="password"
                    v-model="formPsw.sourcePasswd"
                    placeholder="请填写服务器使用的邮箱地址，例如：smtp.qq.com"
                    :disabled="!$hasAccess('per.edit')"
                  ></Input>
                </div>
              </FormItem>

              <FormItem class="setting-item" prop="newPasswd">
                <div class="desc">
                  <div class="name">
                    <span class="red" style="color:red;">*</span>SMTP验证用户
                  </div>
                </div>
                <div class="input">
                  <Input
                    class="input-txt"
                    v-model="formPsw.newPasswd"
                    :maxlength="16"
                    :minlength="6"
                    type="password"
                    :disabled="!$hasAccess('per.edit')"
                    placeholder=请填写使用邮箱地址，例如：abc@.qq.com
                  ></Input>
                </div>
              </FormItem>

              <FormItem class="setting-item" prop="verifyPasswd">
                <div class="desc">
                  <div class="name">
                    <span class="red" style="color:red;">*</span>SMTP验证密码
                  </div>
                </div>
                <div class="input">
                  <Input
                    class="input-txt"
                    v-model="formPsw.verifyPasswd"
                    type="password"
                    :maxlength="16"
                    :minlength="6"
                    :disabled="!$hasAccess('per.edit')"
                    placeholder="请填写邮箱的登录密码"
                  ></Input>
                </div>
              </FormItem>
              <FormItem class="setting-item" prop="verifyPasswd">
                <div class="desc">
                  <div class="name">
                    <span class="red" style="color:red;">*</span>SMTP发件人名称
                  </div>
                </div>
                <div class="input">
                  <Input
                    class="input-txt"
                    v-model="formPsw.verifyPasswd"
                    type="password"
                    :maxlength="16"
                    :minlength="6"
                    :disabled="!$hasAccess('per.edit')"
                    placeholder="请填写邮箱发送的发件人的名称，限制在32个字符以内"
                  ></Input>
                </div>
              </FormItem>

              <div class="info-line"></div>
              <div class="ops" v-if="$hasAccess('per.edit')">
                <!--                <Button @click="handleReset('pasForm')">恢复默认</Button>-->
                <Button type="primary" :loading="isBtnLoading" >{{$t('common.message.save')}}</Button>
              </div>
            </Form>
          </TabPane>
          <TabPane name="3" class="panel-table">
            <div class="tab-box">
              <div class="setting-box setting-banner">
                <div class="setting-item">
                  <div class="desc">
                    <div class="name" v-show="!siteSetting1.banner1">image</div>
                    <div class="img-display" :style="{backgroundImage:`url('${siteSetting1.banner1}')`}" v-show="siteSetting1.banner1"></div>
                    <SimpleImgUpload class="input-log" v-model="siteSetting1.banner1" @on-selection-change="onDeviceModelChange1" v-if="$hasAccess('platform.edit')">
                      <div class="img-add">{{$t_p('customize')}}</div>
                    </SimpleImgUpload>
                  </div>
                  <div class="more">
                    <h4>banner1</h4>
                    <p>{{$t_p('config_format')}}</p>
                    <p>{{$t_p('login_big')}}</p>
                  </div>
                </div>
              </div>
              <div class="setting-box setting-banner">
                <div class="setting-item">
                  <div class="desc">
                    <div class="name" v-show="!siteSetting1.banner2">image</div>
                    <div class="img-display" :style="{backgroundImage:`url('${siteSetting1.banner2}')`}" v-show="siteSetting1.banner2"></div>
                    <div class="input">
                      <SimpleImgUpload class="input-log" v-model="siteSetting1.banner2" @on-selection-change="onDeviceModelChange1" v-if="$hasAccess('platform.edit')">
                        <div class="img-add">{{$t_p('customize')}}</div>
                      </SimpleImgUpload>
                    </div>
                  </div>
                  <div class="more">
                    <h4>banner2</h4>
                    <p>{{$t_p('config_format')}}</p>
                    <p>{{$t_p('login_big')}}</p>
                  </div>
                </div>
              </div>
              <div class="setting-box setting-banner">
                <div class="setting-item">
                  <div class="desc">
                    <div class="name" v-show="!siteSetting1.banner3">image</div>
                    <div class="img-display" :style="{backgroundImage:`url('${siteSetting1.banner3}')`}" v-show="siteSetting1.banner3"></div>
                    <div class="input">
                      <SimpleImgUpload class="input-log" v-model="siteSetting1.banner3" @on-selection-change="onDeviceModelChange1" v-if="$hasAccess('platform.edit')">
                        <div class="img-add">{{$t_p('customize')}}</div>
                      </SimpleImgUpload>
                    </div>
                  </div>
                  <div class="more">
                    <h4>banner3</h4>
                    <p>{{$t_p('config_format')}}</p>
                    <p>{{$t_p('login_big')}}</p>
                  </div>
                </div>
              </div>
              <div class="setting-box setting-banner">
                <div class="setting-item">
                  <div class="desc">
                    <div class="name" v-show="!siteSetting1.banner4">image</div>
                    <div class="img-display" :style="{backgroundImage:`url('${siteSetting1.banner4}')`}" v-show="siteSetting1.banner4"></div>
                    <div class="input">
                      <SimpleImgUpload class="input-log" v-model="siteSetting1.banner4" @on-selection-change="onDeviceModelChange1" v-if="$hasAccess('platform.edit')">
                        <div class="img-add">{{$t_p('customize')}}</div>
                      </SimpleImgUpload>
                    </div>
                  </div>
                  <div class="more">
                    <h4>banner4</h4>
                    <p>{{$t_p('config_format')}}</p>
                    <p>{{$t_p('login_big')}}</p>
                  </div>
                </div>
              </div>
              <div class="setting-box setting-banner">
                <div class="setting-item">
                  <div class="desc">
                    <div class="name" v-show="!siteSetting1.banner5">image</div>
                    <div class="img-display" :style="{backgroundImage:`url('${siteSetting1.banner5}')`}" v-show="siteSetting1.banner5"></div>
                    <div class="input">
                      <SimpleImgUpload class="input-log" v-model="siteSetting1.banner5" @on-selection-change="onDeviceModelChange1" v-if="$hasAccess('platform.edit')">
                        <div class="img-add" >{{$t_p('customize')}}</div>
                      </SimpleImgUpload>
                    </div>
                  </div>
                  <div class="more">
                    <h4>banner5</h4>
                    <p>{{$t_p('config_format')}}</p>
                    <p>{{$t_p('login_big')}}</p>
                  </div>
                </div>
              </div>
            </div>
            <div class="info-line"></div>
            <div class="ops" v-if="$hasAccess('platform.edit')">
              <!--              <Button>恢复默认</Button>-->
              <Button type="primary" @click="saveBanner" >{{$t('common.message.save')}}</Button>
            </div>
          </TabPane>
        </Tabs>
      </div>
    </div>
  </div>
</template>

<script>
  import SimpleImgUpload from '@/components/simple-img-upload'
  import {EditEmail, getBanner, getEmail, getLogo, SaveBanner, SaveLogo} from '@/api/email'

  export default {
    name: 'platform-setting',
    i18n_prefix: 'page.system-setting.platform-setting',
    components: {SimpleImgUpload},

    data () {
      return {
        form: {},
        formPsw: {
          sourcePasswd: '',
          newPasswd: '',
          verifyPasswd: ''
        },
        aa: 11,
        tabs: [{
          name: 'site',
          title: this.$t_p('site_configuration')
        },

          {
            name: 'banner',
            title: this.$t_p('banner_configuration')
          }
          // {
          //   name: "email",
          //   title: this.$t_p('email_configuration')
          // },
          /*,

                 {
                   name: "sms",
                   title: "短信配置"
                 } */
        ],

        siteSetting: {
          browserLogo: '',
          loginLogo: '',
          managerLogo: ''
        },
        logos: {
          browserLogo: '',
          loginLogo: '',
          managerLogo: ''
        },
        siteSetting1: {
          banner1: '',
          banner2: '',
          banner3: '',
          banner4: '',
          banner5: ''
        },
        banners: {
          banner1: '',
          banner2: '',
          banner3: '',
          banner4: '',
          banner5: ''
        },
        userRules: {

        },
        isBtnLoading: false
      }
    },
    methods: {
      handleReset (name) {
        // 或者手动将各个属性置空

        this.$refs[name].resetFields()
      },
      savePasswd () {
        this.isBtnLoading1 = true
        this.checkPasForm().then(
          () => {
            EidtPasswd(this.formPsw).then(
              res => {
                this.isBtnLoading1 = false
                this.$Message.success(res.msg)
              },
              res => {
                this.$Message.warning(res.msg)
              }
            )
          },
          (valid, b) => {
            this.isBtnLoading1 = false
            // this.$Message.error("请检查输入");
          }
        )
      },
      checkPasForm () {
        return new Promise((resolve, reject) => {
          this.$refs.pasForm.validate((valid, b) => {
            if (valid) {
              resolve()
            } else {
              // console.log(b)
              reject(valid, b)
            }
          })
        })
      },
      selectTab (name) {
        this.$router.push({
          name: this.$route.name,
          params: this.$route.params,
          query: {
            ...this.$route.query,
            tab: name
          }
        })
        // if(name=='1'){
        //   this.$nextTick(()=>{
        //     this.$refs.graphViewer && this.$refs.graphViewer.reload();
        //   })
        // }
      },
      checkInfoRefresh (forceUpdate) {
        let tab = this.currentTab
        switch (tab) {
          case '0':
            // this.loadBaseInfo(forceUpdate);
            break
          case '1':
            // this.loadBaseInfo(forceUpdate);
            break
          case '2':
            // this.loadBaseInfo(forceUpdate);
            break
          case '3':
            // this.loadBaseInfo(forceUpdate);
            // this.loadLogTableData(forceUpdate);
            break
        }
      },
      onDeviceModelChange (data) {
        for (let i in this.siteSetting) {
          if (this.siteSetting[i] === data.fileUrl) {
            this.logos[i] = data.id
          }
        }
        // console.log(this.logos)
      },
      onDeviceModelChange1 (data) {
        for (let i in this.siteSetting1) {
          if (this.siteSetting1[i] === data.fileUrl) {
            this.banners[i] = data.id
          }
        }
        // console.log(this.logos)
      },
      getData () {
        getEmail({}).then(res => {
          // console.log(12,res);

          this.form = Object.prototype.toString.call(res.data) === '[object Object]' ? res.data : {}
        })
        getLogo({}).then(res => {
          this.siteSetting.browserLogo = res.data.browserLogo
          this.siteSetting.loginLogo = res.data.loginLogo
          this.siteSetting.managerLogo = res.data.managerLogo
        })
        getBanner({}).then(res => {
          this.siteSetting1.banner1 = res.data.banner1Url
          this.siteSetting1.banner2 = res.data.banner2Url
          this.siteSetting1.banner3 = res.data.banner3Url
          this.siteSetting1.banner4 = res.data.banner4Url
          this.siteSetting1.banner5 = res.data.banner5Url
        })
      },
      saveLogo () {
        SaveLogo(this.logos).then(
          () => {
            this.$Message.success(this.$t('common.message.save_success'))
          },
          () => {
            this.$Message.warning(this.$t('common.message.save_fail'))
          }
        )
      },
      saveBanner () {
        SaveBanner(this.banners).then(
          () => {
            this.$Message.success(this.$t('common.message.save_success'))
          },
          () => {
            this.$Message.warning(this.$t('common.message.save_fail'))
          }
        )
      },
      saveClick () {
        EditEmail(this.form).then(
          res => {
            this.$Message.success(this.$t_p('email_config_success'))
          },
          () => {
            this.$Message.warning(this.$t_p('email_config_fail'))
          }
        )
      }
    },
    computed: {
      currentTab () {
        if (['0', '1', '2', '3', '4', '5', '6', '7', '8'].includes(this.$route.query.tab)) {
          return this.$route.query.tab
        }
        return '0'
      }
    },
    watch: {
      currentTab () {
        this.checkInfoRefresh(true)
      }
    },
    mounted () {
      this.getData()
    }
  }
</script>

<style lang="less" scoped>
  @import "../../../../style/varient";
  .platform-setting {
    width: 100%;
    height: 100%;

    .d-content {
      width: 100%;
      height: calc(~"100% - " 50px);

      .tabs {
        width: 100%;
        margin-top: 6px;
        height: calc(~"100% - " 50px);
        overflow-y: auto;
        background: #FFFFFF;
        border-radius: 8px;
        .panel-table{
          padding: 16px;
          width: 100%;
          height: 100%;
          overflow: auto;
          .tab-box {
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-start;
            .setting-box {
              width: 50%;
              height: 115px;
              padding: 10px;
              .setting-item {
                display: flex;
                justify-items: center;
                align-items: center;
                width: 100%;
                padding: 10px;
                border: 1px solid #dcdcdc;
                background: #fafafa;
                border-radius: 5px;
                .desc {
                  width: 30%;
                  text-align: center;
                  color: @color-primary-change;
                  .name {
                    height: 30px;
                    font-size: 18px;
                    margin-bottom: 6px;
                    font-weight: bold;
                  }
                  .img-display {
                    height: 30px;
                    width: 90px;
                    margin-left: auto;
                    margin-right: auto;
                    margin-bottom: 6px;
                    background-repeat: no-repeat;
                    background-position: center;
                    background-color: white;
                    background-size: contain;
                  }
                  .input-log {
                    width: 70px;
                    border: 1px solid @color-primary-change;
                    margin: 0 auto;
                    line-height: 28px;
                    height: 28px;
                    cursor: pointer;
                  }
                }
                .more {
                  flex: 1;
                  text-align: left;
                  p {
                    height: 26px;
                    line-height: 26px;
                  }
                }
              }
              /*&.setting-banner{*/
              /*  width: 50%;*/
              /*}*/
            }
          }
          &.active {
            display: block;
          }
          .tab-mod {
            .title {
              // font-size: 20px;
              // font-weight: bold;
              height: 50px;
              line-height: 50px;
              font-size: 12px;
              display: flex;
              span:nth-child(1) {
                width: 160px;
                text-align: right;
              }
              span:nth-child(2) {
                width: 30%;
                padding-left: 30px;
                text-align: left;
              }
              span:nth-child(3) {
                flex: 1;
                padding-right: 20px;
                text-align: right;
              }
            }
            form {
              width: 100%;
              .info-header {
                padding: 15px 0 15px 50px;
                font-size: 14px;
              }
              .time-input {
                width: 80px;
              }
              .ivu-form-item {
                width: 50%;
              }
            }
          }
          @size-setting-item-desc-width: 350px;
          .setting-item {
            width: 50%;
            padding: 8px 0 0 0;
            .desc {
              line-height: 1.6;
              .name {
                line-height: 2.2;
              }
              .red {
                color: red;
              }
              .more {
                color: rgba(0, 0, 0, 0.447058823529412);
              }
            }
          }
        }
        .btns {
          display: flex;
          .btn {
            width: 60px;
            height: 30px;
            cursor: pointer;
            text-align: center;
            line-height: 30px;
            background: #FFFFFF;
            border: 1px solid #F0F0F0;
            box-shadow: 1px 2px 3px 0px rgba(0, 0, 0, 0.06);
            border-radius: 4px;
            color: #787878;
          }

          .btn-primary {
            margin-left: 15px;
            color: white;
            line-height: 30px;
            text-align: center;
            width: 60px;
            height: 30px;
            background: linear-gradient(-90deg, @color-primary-change 0%, @color-primary 100%);
            box-shadow: 1px 2px 3px 0px rgba(16, 86, 244, 0.2);
            border-radius: 4px;
          }
        }

        .base-table {
          width: 100%;
          border-top: 1px solid #F0F0F0;
          border-left: 1px solid #F0F0F0;
          border-right: 1px solid #F0F0F0;
          border-radius: 4px;

          .base-tr {
            width: 100%;
            height: 36px;
            line-height: 36px;
            border-bottom: 1px solid #F0F0F0;
            display: flex;
            font-size: 12px;

            .base-td:nth-child(2n) {
              color: #1F1F1F;
              width: calc(~"33.3% - " 120px);
              display: flex;
              justify-content: center;
              align-items: center;
              padding: 4px;

              span {
                padding: 0 10px;
                overflow: hidden; /*超出部分隐藏*/
                white-space: nowrap; /*不换行*/
                text-overflow: ellipsis; /*超出部分文字以...显示*/
              }

              &.base-line {
                width: calc(~"100% - " 120px);
              }
            }

            .base-td:nth-child(2n+1) {
              text-align: right;
              padding: 0 15px;
              color: #787878;
              background: #F7F8FC;
              width: 120px;
            }

          }
        }
      }
      .info-line{
        width: 100%;
        height: 1px;
        margin: 15px auto;
        background: #d3d3d3;
      }
      .ops{
        padding-left: 20px;
      }
    }
  }
</style>



