<template>
  <div class="simple-img-upload">
    <Upload
      ref="upload"
      :headers="uploadHeaders"
      :format="['jpg','jpeg','png']"
      :max-size="2048"
      :show-upload-list="false"
      class="upload"
      name="file"
      :data="{fileType:1}"
      :before-upload="beforeUpload"
      :on-progress="onFileProgress"
      :on-success="onUploadSuccess"
      :on-error="onUploadError"
      :action="uploadUrl">
      <slot></slot>
    </Upload>
  </div>
</template>

<script>
  import {fileUploadUrl} from '@/api/file'

  import {getToken} from '@/libs/util'

  export default {
    name: "simple-img-upload",
    i18n_prefix:'component.form-img-upload',
    data() {
      return {
        uploadUrl: fileUploadUrl,
        uploadHeaders: {
          "token": getToken()
        }
      }
    },
    props: {
      value: {
        type: String
      }
    },
    model: {
      prop: 'value',
      event: 'on-change'
    },
    methods: {
      deleteFile(file, index) {
        let files = [].concat(this.value || []);
        files.splice(index, 1);
        this.$emit('on-change', files)
      },

      beforeUpload(file) {

      },

      onFileProgress(event, file, fileList) {
        // console.log('onFileProgress',event, file, fileList)
        // console.log(file)
        this.$emit('on-progress', file);
      },
      onUploadSuccess(event, file, fileList) {
        // console.log('onUploadSuccess',event, file, fileList)
        if (event.code == 200) {
          this.$emit('on-success',file);
          this.$emit('on-change', event.data.fileUrl);
          this.$emit("on-selection-change", event.data);
        } else {
          this.$emit('on-error',file);
        }

      },
      onUploadError(event, file, fileList) {
        this.$emit('on-error',file);
        this.$Message.error(this.$t_p('upload_fail'))
      },

    }

  }
</script>

<style lang="less" scoped>
  .simple-img-upload {

  }
</style>



