import axios from '@/libs/api.request'

/**
 * 修改邮件
 * @param serverUrl SMTP服务器地址
 * @param verifyUser SMTP验证用户
 * @param verifyPasswd SMTP验证密码
 * @param verifyName SMTP发件人名称
 * @returns {*}
 */
export const EditEmail = ( {serverUrl, verifyUser, verifyPasswd, verifyName }) =>  {
    const data =  {
        serverUrl, 
        verifyUser, 
        verifyPasswd, 
        verifyName
    }; 
    return axios.request( {
      url:'/email/saveEmailConfig', 
      data, 
      method:'post'
    })
  }; 

/**
 * 查询邮件配置
 * @returns {*}
 */
export const getEmail = ({ }) => {
  const data = {};
  return axios.request({
    url: '/email/getEmailConfig',
    params:data, 
    method: 'get'
  })
};

/**
 * 查询站点配置
 * @returns {*}
 */
export const getLogo = ({ }) => {
  const data = {};
  return axios.request({
    url: '/logo/getLogoConfig',
    params:data, 
    method: 'get'
  })
};

/**
 * 保存站点配置
 * @param loginLogo 登陆logo id
 * @param managerLogo 后台logo id
 * @param browserLogo 浏览器logo id
 * @returns {*}
 */
export const SaveLogo = ( {loginLogo, managerLogo, browserLogo}) =>  {
  const data =  {
    loginLogo, 
    managerLogo, 
    browserLogo
  }; 
  return axios.request( {
    url:'/logo/saveLogoConfig', 
    data, 
    method:'post'
  })
}; 

/*banner图获取*/
export const getBanner = ({ }) => {
  const data = {};
  return axios.request({
    url: '/logo/getBannerConfig',
    params:data, 
    method: 'get'
  })
};

/*banner图上传*/
export const SaveBanner = ( {banner1, banner2, banner3, banner4, banner5}) =>  {
  const data =  {
    banner1, 
    banner2, 
    banner3, 
    banner4, 
    banner5
  }; 
  return axios.request( {
    url:'/logo/saveBannerConfig', 
    data, 
    method:'post'
  })
}; 



