import axios from '@/libs/api.request'
import {getToken} from '@/libs/util'
// 批量导出报警记录
export const alarmExport = ({
                              deviceName,
                              sDate,
                              eDate,
                              severity
                            }) =>  {
  // window.open(`${axios.baseUrl}/asset/exportLabels?nameOrNum=${nameOrNum}&token=${getToken()}`)
  window.open(`${axios.baseUrl}/alarmInfo/export?severity=${severity}&deviceName=${deviceName}&sDate=${sDate}&eDate=${eDate}&token=${getToken()}`)
  return Promise.resolve()
};

export const workExport = ({
                             worderType,status,start,end,name
                            }) =>  {
  // window.open(`${axios.baseUrl}/asset/exportLabels?nameOrNum=${nameOrNum}&token=${getToken()}`)
  window.open(`${axios.baseUrl}/order/export?name=${name}&worderType=${worderType}&status=${status}&start=${start}&end=${end}&token=${getToken()}`)
  return Promise.resolve()
};
export const fileUploadUrl = `${axios.baseUrl}/file/uploadFile`;
export const fileUpUrl = ({ fileId}) => {
    const data = {
        fileId
    };
    return axios.request({
      url: 'file/download',
      params: data,
      method: 'GET'
    })
  };



